* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  max-width: 320px;
  box-shadow: none;
  border: 1px solid #eee;
  border-radius: 10px;
  margin-bottom: 16px;
  padding: 8px;
  transition: border-color .3s, box-hadow .3s;
}

#search-box:hover, .country-info:hover {
  border: 1px solid #20bcc6;
  box-shadow: 16px 12px 8px rgba(32, 188, 198, .2);
}

#search-box:focus {
  box-shadow: 16px 12px 8px rgba(0, 0, 0, .2);
}

.country-list {
  list-style: none;
}

.country-list li {
  align-items: center;
  display: flex;
}

.country-list p {
  margin-left: 10px;
}

